<template>
  <div class="bs-home-manager">
    <el-table :data="managerList" style="width: 100%" max-height="250">
      <el-table-column fixed prop="create_time" label="创建日期" width="200"></el-table-column>
      <el-table-column prop="username" label="姓名" width="150"></el-table-column>
      <el-table-column prop="remark" label="权限" width="200"></el-table-column>
      <el-table-column prop="sex" label="性别" width="120"></el-table-column>
      <el-table-column prop="email" label="邮箱" width="800"></el-table-column>
      <el-table-column fixed="right" label="操作" width="380">
        <template slot-scope="scope">
          <el-button
            @click.native.prevent="deleteRow(scope.$index, managerList)"
            type="text"
            size="small"
          >移除</el-button>
          <el-button
            @click.native.prevent="seeMessage(scope.$index, managerList)"
            type="text"
            size="small"
          >查看</el-button>
          <el-button
            @click.native.prevent="modifyMessage(scope.$index, managerList)"
            type="text"
            size="small"
          >修改</el-button>
        </template>
      </el-table-column>
      <input type="text" />
    </el-table>
    <addManager @addSuccess="addSuccess"></addManager>
  </div>
</template>
<script>
import * as ajax from "@/api";
import addManager from "@/components/Manager/addManager";

export default {
  data() {
    return {
      managerList: [],
      modifyusername: "",
      modifyuseremail: "",
      modifyusersex: "",
      radio2: 0
    };
  },
  created() {
    ajax.getManagerList().then(resp => {
      if (resp.data.code) {
        let list = resp.data.data.manager_list.forEach(element => {
          if(!element['remark']){
            element['remark'] = '无权限'
          } 
        });
        this.managerList = resp.data.data.manager_list;
      }
    });
  },
  methods: {
    deleteRow(index, rows) {
      rows.splice(index, 1);
    },
    seeMessage(index, list) {
      this.$alert(
        `<strong>用户名：${list[index]["username"]}</strong>
        <p>创建时间:${list[index]["create_time"]}</p>
        <p>邮箱:${list[index]["email"]}</p>
        <p>权限:${list[index]["remark"] ? list[index]["remark"] : "无权限"}</p>
        <p>性别:${list[index]["sex"]}</p>
        <p>是否禁用:${list[index]["is_active"] ? "未禁用" : "禁用"}</p>
        `,
        "管理员信息",
        {
          dangerouslyUseHTMLString: true
        }
      );
    },
    addSuccess(data) {
      this.managerList = [data, ...this.managerList];
    }
  },
  components: {
    addManager
  }
};
</script>

<style lang='scss'>
.bs-home-manager {
  flex: 1;
  .el-table {
    .cell {
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
    .el-table__body-wrapper {
      overflow-y: hidden;
    }
    max-height: 100% !important;
    .el-table__body-wrapper {
      max-height: 100% !important;
    }
  }
  .el-table__fixed-body-wrapper {
    max-height: 100% !important;
  }
}
</style>