<template>
  <div class="bs-home-add-manager">
    <div class="bs-add-manager-header">添加管理员</div>
    <el-form
      :model="dynamicValidateForm"
      ref="dynamicValidateForm"
      label-width="100px"
      class="demo-dynamic"
    >
      <el-form-item
        prop="email"
        label="邮箱"
        :rules="[
            { required: true, message: '请输入邮箱地址', trigger: 'blur' },
            { type: 'email', message: '请输入正确的邮箱地址', trigger: 'blur,change' }
            ]"
      >
        <el-input v-model="dynamicValidateForm.email"></el-input>
      </el-form-item>

      <el-form-item prop="email" label="用户名">
        <el-input v-model="username"></el-input>
      </el-form-item>
    </el-form>

    <el-form
      :model="ruleForm2"
      status-icon
      :rules="rules2"
      ref="ruleForm2"
      label-width="100px"
      class="demo-ruleForm"
    >
      <el-form-item label="密码" prop="pass">
        <el-input type="password" v-model="ruleForm2.pass" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="确认密码" prop="checkPass">
        <el-input type="password" v-model="ruleForm2.checkPass" auto-complete="off"></el-input>
      </el-form-item>
    </el-form>

    <div class="block">
        身份：
      <el-cascader  :options="options" v-model="selectedOptions"></el-cascader>
    </div>
    <el-button type="success" @click="handleAddRole">添加</el-button>
  </div>
</template>

<script>
import * as ajax from "@/api";

export default {
  data() {
    var validatePass = (rule, value, callback) => {
      if (value.length < 6) {
        callback(new Error("密码需要超过6位数"));
      }
      if (value === "") {
        callback(new Error("请输入密码"));
      } else {
        if (this.ruleForm2.checkPass !== "") {
          this.$refs.ruleForm2.validateField("checkPass");
        }
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        this.password = "";
        callback(new Error("请再次输入密码"));
      } else if (value !== this.ruleForm2.pass) {
        this.password = "";
        callback(new Error("两次输入密码不一致!"));
      } else {
        callback();
        this.password = value;
      }
    };
    return {
      dynamicValidateForm: {
        domains: [
          {
            value: ""
          }
        ],
        email: ""
      },
      username: "",
      ruleForm2: {
        pass: "",
        checkPass: "",
      },
      rules2: {
        pass: [{ validator: validatePass, trigger: "blur" }],
        checkPass: [{ validator: validatePass2, trigger: "blur" }]
      },
      selectedOptions: [],
      options: []
    };
  },
  methods: {
   handleAddRole(){
       if(this.email == ''){
           alert('请输入邮箱')
       }else if(this.ruleForm2.pass !== this.ruleForm2.checkPass){
           alert('两次密码不一样，请确认密码')
       }else if(!this.ruleForm2.pass){
           alert('密码不能为空')
       }else if(!this.selectedOptions){
           alert('请选择管理员身份')
       }else if(!this.username){
           alert('用户名不能为空')
       }else{
           ajax.addRole(this.username,this.ruleForm2.pass,this.selectedOptions[0],this.dynamicValidateForm.email,this.options[this.selectedOptions[0]].label).then(resp=>{
               if(resp.data.code){
                   alert('添加成功')
                   this.$emit('addSuccess',resp.data.data)
               }
           })
       }
   }
    
  },
  created() {
    ajax.getRoleList().then(resp => {
      if (resp.data.code) {
        this.options = resp.data.data.role_list;
      }
    });
  }
};
</script>

<style lang='scss'>
.bs-home-add-manager {
  .bs-add-manager-header {
    height: 50px;
    margin-top: 30px;
    background-color: rgb(189, 189, 34);
    line-height: 50px;
    color: #fff;
    padding-left: 10px;
  }
  .el-form {
    .el-form-item {
      margin-top: 10px;
      .el-input {
        width: 30%;
      }
    }
  }
  .el-button--success {
    margin-left: 100px;
    margin-top: 10px;
  }
  .block{
      padding-left: 47px;
  }

}
</style>